
















































import { Component, Prop, Vue } from "vue-property-decorator";
import { commentList, comment } from "../api";

@Component
export default class Comment extends Vue {
  @Prop() value!: any[];
  platform = "jingdong";
  tableData: any[] = [];
  multipleSelection: any[] = [];
  more = false;
  page = 1;
  pending = false;
  onClick() {
    this.page = 1;
    this.refresh();
  }
  async refresh() {
    var ret = await commentList(
      {
        page: this.page,
        type: 6
      },
      this.platform
    );
    this.tableData = ret.items;
    this.more = ret.more;
  }

  go(num: number) {
    this.page += num;
    this.refresh();
  }

  async comment() {
    this.pending = true;
    try {
      await comment(
        {
          orderIds: this.multipleSelection.map(item => item.id)
        },
        this.platform
      );
    } catch (e) {}
    this.refresh();
    this.pending = false;
  }

  onSelectionChange(val: any) {
    this.multipleSelection = val;
  }
}
